.form-control-submit {
  button, input[type=submit] {
    //margin-right: 5px;
  }
  a {
    margin-left: 10px;
    text-decoration: underline;
  }
}

.ckeditor-container {
  border-left: 1px solid #cccccc;
  border-right: 1px solid #cccccc;
}

.fileinput-button {
  position: relative;
  overflow: hidden;
  display: inline-block;
}
.fileinput-button input {
  position: absolute;
  top: 0;
  right: 0;
  margin: 0;
  opacity: 0;
  -ms-filter: 'alpha(opacity=0)';
  font-size: 200px !important;
  direction: ltr;
  cursor: pointer;
}

#progress {
  border: 1px solid #999999;
  display: none;
  height: 10px;
  width: 300px;
  margin-top: 10px;
}

.progress-bar {
  height: 8px;
  width: 0%;
  background-color: #ffcc00;
}
